import Vue from 'vue'
import App from './App.vue'
import router from './router'

// //从vant组件库导入Button组件
// import { Button } from 'vant'

//根据屏幕适配rem
import 'lib-flexible/flexible'

import {
  Row,
  Col,
  Tabbar,
  TabbarItem,
  SubmitBar,
  Checkbox,
  CheckboxGroup,
  Stepper,
  RadioGroup,
  Radio,
  NavBar
} from 'vant';
// 全局注册组件
Vue
  .use(Row).use(Col)
  .use(Tabbar)
  .use(TabbarItem)
  .use(SubmitBar)
  .use(Checkbox)
  .use(CheckboxGroup)
  .use(Stepper)
  .use(RadioGroup)
  .use(Radio)
  .use(NavBar)

// Vue.use(Button)

Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
